
/* wendy-one-regular - latin-ext_latin */
@font-face {
    font-family: 'Wendy One';
    font-style: normal;
    font-weight: 400;
    src: url('fonts/wendy-one-v8-latin-ext_latin-regular.eot'); /* IE9 Compat Modes */
    src: local('Wendy One'), local('WendyOne-Regular'),
         url('fonts/wendy-one-v8-latin-ext_latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fonts/wendy-one-v8-latin-ext_latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('fonts/wendy-one-v8-latin-ext_latin-regular.woff') format('woff'), /* Modern Browsers */
         url('fonts/wendy-one-v8-latin-ext_latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fonts/wendy-one-v8-latin-ext_latin-regular.svg#WendyOne') format('svg'); /* Legacy iOS */
  }

